html {
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
  padding-inline-start: 0;
}

.backdrop {
  z-index: 160;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: #0000004d;
  transition: opacity .1s ease-out;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: hidden;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop.is-hidden .modal {
  z-index: 160;
  transform: translate(-50%, -100%)scale(.7);
}

.modal {
  width: 280px;
  z-index: 6;
  max-height: 100vh;
  background-color: #fff;
  border-radius: 4px;
  padding: 48px 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
  box-shadow: 0 4px 4px #00000040;
}

.modal-close-button {
  width: 30px;
  height: 30px;
  box-shadow: none;
  cursor: pointer;
  background-color: #fff;
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  padding-right: 0;
  transition: background-color .5s ease-out;
  display: flex;
  position: absolute;
  top: 10px;
  right: 10px;
}

.modal-close-button:hover, .modal-close-button:focus {
  background-color: #ff6b08;
}

